// 获取热门歌手数据
export const getHotArtists = () => {
  return new Promise(function (resolve, reject) {
    Network.get('top/artists?offset=0&limit=10').then(function (result) {
      // console.log(result)
      const list = []
      result.artists.forEach(function (value) {
        const obj = {}
        obj.singer = value.name
        obj.singerID = value.id
        obj.singerImg = value.picUrl
        list.push(obj)
      })
      // console.log(list)
      resolve(list)
    }).catch(function (err) {
      reject(err)
    })
  })
}
// 获取歌手分类数据
export const getLetterArtists = (letter) => {
  return new Promise(function (resolve, reject) {
    // const letterArtists = []
    Network.all([
      Network.get(`artist/list?offset=0&limit=10&area=-1&initial=${letter}`)
    ])
      .then(function (result) {
        // console.log(result)
        // console.log(result[0].artists)
        const list = []
        result[0].artists.forEach(function (value) {
          // console.log(value)
          const obj = {}
          obj.singer = value.name
          obj.singerID = value.id
          obj.singerImg = value.picUrl
          // console.log(obj)
          list.push(obj)
        })
        // console.log(list)
        resolve(list)
      })
      .catch(function (err) {
        reject(err)
      })
  })
}
// 热门歌手数据 和 歌手分类数据整合
export const getAllArtists = () => {
  return new Promise(function (resolve, reject) {
    const keys = ['热']
    const list = [getHotArtists()]
    for (let i = 65; i < 91; i++) {
      const char = String.fromCharCode(i)
      keys.push(char)
      list.push(getLetterArtists(char))
    }
    Network.all(list)
      .then(function (result) {
        // console.log(result)
        const obj = {}
        obj.keys = keys
        obj.list = result
        // console.log(obj)
        resolve(obj)
      })
      .catch(function (err) {
        reject(err)
      })
  })
}
