// 获取所有榜单的数据
export const getTopListDetail = () => {
  return new Promise(function (resolve, reject) {
    const category = {
      officialList: [
        { name: '飙升榜', id: 3 },
        { name: '新歌榜', id: 0 },
        { name: '原创榜', id: 2 },
        { name: '热歌榜', id: 1 }
      ],
      recList: [
        { name: '黑胶VIP爱听榜', id: 23 },
        { name: '云音乐说唱榜', id: 25 },
        { name: '云音乐古典榜', id: 32 },
        { name: '云音乐ACG游戏榜', id: 26 },
        { name: '云音乐ACG VOCALOID榜', id: 22 },
        { name: '中国新乡村音乐排行榜', id: 24 }
      ],
      globalList: [
        { name: 'iTunes榜', id: 6 },
        { name: '日本Oricon榜', id: 5 },
        { name: '云音乐欧美热歌榜', id: 21 },
        { name: '云音乐欧美新歌榜', id: 10 },
        { name: '法国 NRJ Vos Hits 周榜', id: 8 },
        { name: '云音乐ACG动画榜', id: 29 }
      ],
      otherList: [
        { name: '云音乐日语榜', id: 7 },
        { name: '云音乐摇滚榜', id: 19 },
        { name: '云音乐古风榜', id: 27 },
        { name: '潜力爆款榜', id: 28 },
        { name: '云音乐民谣榜', id: 30 },
        { name: '听歌识曲榜', id: 31 }
      ],
      titles: {
        officialList: '官方榜',
        recList: '推荐榜',
        globalList: '全球榜',
        otherList: '更多榜单'
      }
    }
    Network.get('toplist/detail')
      .then(function (data) {
        // 对拿到的数据进行遍历
        data.list.forEach(function (value) {
          let flag = false
          // 对自行遍历category
          for (const key in category) {
            // console.log(category[key])
            // 取出每个category[key]里的对象的name
            for (let i = 0; i < category[key].length; i++) {
              if (category[key][i].name === value.name) {
                category[key][i].id = value.id
                category[key][i].Img = value.coverImgUrl
                category[key][i].update = value.updateFrequency
                category[key][i].songs = value.tracks
                // console.log(category[key][i])
                flag = true
                break
              }
            }
            if (flag) {
              break
            }
          }
        })
        resolve(category)
      })
      .catch(function (err) {
        reject(err)
      })
  })
}
