<template>
  <transition
    :css="false"
    @enter="enter"
    @leave="leave">
      <div class="mini-player" v-show="this.isShowMinPlayer">
        <div class="player-wrapper">
          <div class="player-left" @click="showNormalPlayer">
            <img :src="currentSong.picUrl" alt="" ref="cdWrapper">
            <div class="player-title">
              <h3>{{ currentSong.name }}</h3>
              <p>{{ currentSong.singer }}</p>
            </div>
          </div>
          <div class="player-right">
            <div class="play" @click="play" ref="play"></div>
            <div class="list" @click.stop="showList"></div>
          </div>
        </div>
      </div>
  </transition>
</template>
<script>
import { mapActions, mapGetters } from 'vuex'
import Velocity from 'velocity-animate'
import 'velocity-animate/velocity.ui'
export default {
  name: 'MiniPlayer',
  methods: {
    ...mapActions([
      'setFullScreen',
      'setIsPlaying',
      'setListScreen'
    ]),
    showList () {
      this.setListScreen(true)
    },
    showNormalPlayer () {
      this.setFullScreen(true)
    },
    enter (el, done) {
      Velocity(el, 'transition.shrinkIn', { duration: 300 }, function () {
        done()
      })
    },
    leave (el, done) {
      Velocity(el, 'transition.shrinkOut', { duration: 300 }, function () {
        done()
      })
    },
    play () {
      this.setIsPlaying(!this.isPlaying)
    }
  },
  computed: {
    ...mapGetters([
      'isPlaying',
      'currentSong',
      'isShowMinPlayer'
    ])
  },
  watch: {
    isPlaying (newValue) {
      if (newValue) {
        this.$refs.play.classList.add('active')
        this.$refs.cdWrapper.classList.add('active')
      } else {
        this.$refs.play.classList.remove('active')
        this.$refs.cdWrapper.classList.remove('active')
      }
    }
  }
}
</script>

<style scoped lang="scss">
@import "../../assets/css/variable";
@import "../../assets/css/mixin";
  .mini-player{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
    height: 150px;
    .player-wrapper{
      width: 100%;
      height: 100%;
      @include bg_color();
      display: flex;
      justify-content: space-between;
      align-items: center;
      .player-left{
        display: flex;
        align-items: center;
        img{
          width: 100px;
          height: 100px;
          border-radius: 50%;
          margin: 0 20px;
          animation: sport 10s linear infinite;
          //设置动画的默认状态
          animation-play-state: paused;
          &.active{
            animation-play-state: running;
          }
        }
        .player-title{
          width: 60%;
          color: white;
          padding-left: 20px;
          h3{
            line-height: 60px;
            @include clamp(1);
            @include font_size($font_medium);
          }
          p{
            line-height: 60px;
            @include clamp(1);
            @include font_size($font_medium_s);
          }
        }
      }
      .player-right{
        display: flex;
        align-items: center;
        .play{
          width: 84px;
          height: 84px;
          @include bg_img('../../assets/images/play');
          &.active{
            @include bg_img('../../assets/images/pause');
          }
        }
        .list{
          width: 120px;
          height: 120px;
          @include bg_img('../../assets/images/list')
        }
      }
    }
  }
@keyframes sport {
  from{
    transform: rotate(0deg);
  }
  to{
    transform: rotate(360deg);
  }
}
</style>
